<template>
  <div v-if="progressRemind === 0" class="blank-bg text-center"><img src="//qianka.b0.upaiyun.com/images/dd7071ab9572590feef74320ce5d509b.png" alt=""></div>
  <div v-else-if="progressRemind === 1" class="credit-progress">
    <div class="check-time-remind text-center">核卡需1周，通过后<span class="assertive">无需激活</span>奖励2周内到账</div>
    <list class="list list-ios">
      <item class="progress-card" v-for="(p, index) in progress" :key="index">
        <div class="icon" :class="{'checking': p.checking, 'fail': !p.checking}"><img :src="p.icon" alt=""></div>
        <div class="information">
          <div class="title" v-text="p.title"></div>
          <div v-if="p.checking" class="check-status checking">银行审核中…</div>
          <div v-if="!p.checking" class="check-status fail">审核失败</div>
          <div class="name user-info">姓名：{{p.username}}</div>
          <div class="mobile user-info">手机：{{p.mobile}}</div>
          <div class="date user-info" v-text="p.date"></div>
        </div>
        <div v-if="p.checking" class="btn text-center" @click="queryProgress(p)"><span>查询进度</span></div>
        <div v-if="!p.checking" class="btn-apply text-center" @click="apply()"><span>申诉</span></div>
      </item>
    </list>
    </div>
  </div>
</template>
<style lang="scss">
  @import '../../../scss/mixins';

  .blank-bg {
    img {
      margin-top: 133px;
      width: 136px;
      height: 168px;
    }
  }

  .credit-progress {
    .check-time-remind {
      margin: 64px auto 10px;
      width: 280px;
      height: 40px;
      background: #FFF8EE;
      font-size: 12px;
      color: #3A3A3A;
      line-height: 40px;

      @include font-regular();

      span {
        @include font-bold();
      }
    }

    .progress-card {
      position: relative;
      height: 146px;
      overflow: visible;

      .icon {
        position: absolute;
        top: 17px;
        width: 67px;
        height: 44px;
        border-radius: 4px;

        &.checking {
          border: 1px solid #4A90E2;
        }

        &.fail {
          border: 1px solid #EF4F2A;
        }

        img {
          padding-top: 1px;
          padding-left: 1px;
          width: 64px;
          height: 41px;
        }
      }

      .information {
        .title {
          position: absolute;
          left: 76px;
          height: 22px;
          line-height: 22px;
          font-size: 16px;
          color: #3A3A3A;

          @include font-regular();
        }

        .check-status {
          position: absolute;
          top: 45px;
          left: 76px;
          line-height: 18px;
          height: 18px;
          font-size: 13px;

          @include font-medium();

          &.checking {
            color: #4A90E2;
          }

          &.fail {
            color: #EF4F2A;
          }
        }

        .name {
          top: 73px;
        }

        .mobile {
          top: 93px;
        }

        .date {
          top: 113px;
        }

        .user-info {
          position: absolute;
          left: 76px;
          font-size: 13px;
          color: #B5B5B5;
          letter-spacing: 0;
          line-height: 18px;
        }
      }

      .btn {
        position: absolute;
        top: 25px;
        right: 16px;
        background: #4990E2;
        border-radius: 2px;
        width: 68px;
        height: 30px;

        span {
          font-size: 12px;
          height: 30px;
          line-height: 30px;
          color: #FFFFFF;
          letter-spacing: 0;

          @include font-AppleSystemUIFont();
        }
      }

      .btn-apply {
        position: absolute;
        top: 25px;
        right: 16px;
        border: 1px solid #EF4F2A;
        border-radius: 2px;
        width: 68px;
        height: 30px;

        span {
          font-size: 12px;
          line-height: 30px;
          height: 30px;
          color: #EF4F2A;

          @include font-medium();
        }
      }

      .hairline-top::before {
        visibility: hidden;
      }

      .hairline-bottom::after {
        visibility: hidden;
      }
    }
  }
</style>
<script>
  import { getCustomerServiceLink } from 'utils'
  import { popup_credit_weixin_task } from '../popups.js'

  export default {
    props: {
      progress: Array,
      progressRemind: Number
    },

    methods: {
      queryProgress (task) {
        if (task.need_popup === 1) {
          let popup = $popup.fromTemplate(
            popup_credit_weixin_task.template(task.bank_url),
            popup_credit_weixin_task.options
          )
          popup.show()
        } else {
          window.location.href = task.bank_url
        }
      },

      apply () {
        window.location.href = getCustomerServiceLink()
      }
    }
  }
</script>



