/* eslint eqeqeq: "off", camelcase: "off" */
require('./popups.scss')

// 申请信用卡成功-弹层
const popup_credit_apply_success = {
  options: {
    effect: 'scale',
    cssClass: 'popup-credit-apply-success',
    buttons: [
      {text: '知道了'}
    ]
  },

  template: (desc) => {
    return `
      <div class="title">申请成功</div>
      <div class="desc">${desc}</div>
    `
  }
}

// 前往银行官网申请-弹层
const popup_credit_go_to_blank = {
  options: {
    effect: 'scale',
    cssClass: 'popup-credit-go-to-blank',
    showClose: true,
    buttons: [
      {text: '前往银行官网申请'}
    ]
  },

  template: () => {
    return `
      <img class="logo" src="//qianka.b0.upaiyun.com/images/dd2f33c8db513656ff136bde1f14a8b1.png">
      <div class="desc">申请资料<span class="special-color">真实全面</span>，多提供工作、财力、学历等证明，能提高办卡通过率哟</div>
      <div class="remind-wrapper">
        <input type="checkbox" id="checkbox_remind" class="checkbox_none" name="checkbox1" value="not_remind">
        <label for="checkbox_remind">
          <img src="//qianka.b0.upaiyun.com/images/cb75253230cfb5d70f1e2f00791c0eb7.png" class="checkmark-empty"/>
          <img src="//qianka.b0.upaiyun.com/images/d3b50c2afc269dfae839f235e58b2e31.png" class="checkmark-checked">
          <span class="checkbox-remind-text">不再提醒</span>
        </label>
      </div>
    `
  }
}

// 申请信用卡成功-弹层
const popup_credit_has_apply_once = {
  options: {
    effect: 'scale',
    cssClass: 'popup_credit_has_apply_once',
    buttons: [
      {text: '等待审批'},
      {text: '前往办卡'}
    ]
  },

  template: `
    <p class="desc text-center">该手机号已提交过，银行审批通过后奖励将在7个工作日内发放</p>
  `
}

// 前往银行官网申请-弹层(新)
const popup_credit_forward_blank = {
  options: {
    effect: 'scale',
    cssClass: 'popup_credit_forward_blank',
    showClose: true,
    buttons: [
      {text: '前往银行官网申请'}
    ]
  },

  template: (phone) => {
    return `
      <div class="phone text-center">${phone}</div>
      <div class="desc text-center">请确认使用该手机号申请信用卡申请资料越真实全面，通过率越高</div>
      <div class="remind-wrapper">
      </div>
    `
  }
}

//  微信任务进度查询弹层提示
const popup_credit_weixin_task = {
  options: {
    effect: 'scale',
    cssClass: 'popup-credit-weixin-task',
    showClose: true
  },
  template: (weixinTaskUrl) => {
    let white_png = 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAGXRFWHRTb2Z0d2FyZQBBZG9iZSBJbWFnZVJlYWR5ccllPAAAABBJREFUeNpi/v//PwNAgAEACQsDAUdpTjcAAAAASUVORK5CYII='

    return `
      <div class="background-transparent"></div>
      <div class="link-content">
        <div class="link-copy-area">
          <img class="copy-img" src="${white_png}" alt="${weixinTaskUrl}">
          <div class="copy-select"></div>
          <div class="copy-text"><span>${weixinTaskUrl}</span></div>
        </div>
        <div class="info">在微信里点击该链接可查询进度</div>
      </div>
      <div class="background-transparent desc">长按链接可拷贝</div>
    `
  }
}

// 信用卡 显示城市弹层
const pop_credit_city = {
  options: {
    effect: 'scale',
    cssClass: 'popup-credit-city',
    buttons: [
      {text: '知道了'}
    ]
  },
  template: (cityData) => {
    return `
      <div class="desc">
       ${cityData}
      </div>
    `
  }
}

module.exports = {
  popup_credit_apply_success,
  popup_credit_go_to_blank,
  popup_credit_has_apply_once,
  popup_credit_forward_blank,
  popup_credit_weixin_task,
  pop_credit_city
}



