<template>
  <div class="credit-card">
    <div class="credit-reward-remind text-center" v-if="!isEmpty">
      <span>同一银行可让家人朋友申请，拿多次奖励</span>
    </div>
    <div class="empty-list text-center" v-if="isEmpty">
      <img src="//qianka.b0.upaiyun.com/images/c7ef42077e19f6971ceacb35940960c2.png" alt="No Data">
    </div>
    <list class="list list-ios">
      <item class="task-card" v-for="(t, index) in tasks" :key="index" @click.native="taskClicked(t)">
        <div class="icon-and-num">
          <div class="icon"><img :src="t.icon" alt=""></div>
          <div class="applied-num text-center"><span v-text="t.applied_num + '万人申请'"></span></div>
        </div>
        <div class="information">
          <div class="title" v-text="t.title"></div>
          <div class="tags-wrapper">
            <div v-for="(tag, index) in t.tags" :key="index">
              <div class="circle"></div>
              <div class="desc" v-text="t.tags[index]"></div>
            </div>
          </div>
        </div>
        <scalable class="reward" width="80" height="40">
          <span class="yuan">元</span>
          <span class="amount">{{ (t.user_reward)}}</span>
          <span class="plus">＋</span>
        </scalable>
      </item>
    </list>
  </div>
</template>
<style lang="scss">
  @import '../../../scss/mixins';

  .credit-card {
    .credit-reward-remind {
      margin: 64px auto 10px;
      width: 256px;
      height: 40px;
      background: #FFF8EE;
      line-height: 40px;

      span {
        font-size: 12px;
        color: #3A3A3A;
        line-height: 17px;

        @include font-regular;
      }
    }

    .empty-list {
      img {
        margin-top: 134px;
        width: 148px;
        height: 168px;
      }
    }

    .task-card {
      height: 99px;
      overflow: visible;

      .icon-and-num {
        position: absolute;
        top: 19px;

        .icon {
          border: 0.5px solid #DDDDDD;
          border-radius: 4px;
          width: 66px;
          height: 42px;

          img {
            padding-top: 1px;
            padding-left: 1px;
            width: 64px;
            height: 40px;
          }
        }

        .applied-num {
          margin-top: 4px;
          margin-left: -2px;
          background: #F5F5F5;
          border-radius: 50px;
          width: 67px;
          height: 16px;

          span {
            font-size: 10px;
            color: #888888;
            letter-spacing: 0;
          }
        }
      }

      .information {
        position: relative;
        left: 76px;

        .title {
          font-size: 16px;
          height: 22px;
          line-height: 22px;
          color: #3A3A3A;

          @include font-regular;
        }

        .tags-wrapper {
          position: absolute;
          top: 30px;

          .circle {
            display: inline-block;
            background: #E4E4E4;
            border-radius: 50%;
            width: 4px;
            height: 4px;
            margin-bottom: 2px;
          }

          .desc {
            display: inline-block;
            font-size: 12px;
            height: 17px;
            line-height: 17px;
            color: #888888;
            letter-spacing: 0;
            margin-bottom: 1px;
          }
        }
      }

      .reward {
        position: absolute;
        top: 20px;
        right: 16px;
        color: $assertive;

        span {
          display: block;
          float: right;
        }

        .plus, .yuan {
          font-size: 22px;
          line-height: 22px;

          @include font-medium();
        }

        .plus {
          margin-top: 36px;
        }

        .yuan {
          margin-top: 36px;
        }

        .amount {
          font-size: 48px;
          line-height: 80px;
          margin: 0 4px;

          @include font-medium();
        }
      }

      .hairline-top::before {
        visibility: hidden;
      }

      .hairline-bottom::after {
        visibility: hidden;
      }
    }
  }
</style>
<script>
  export default {
    props: {
      tasks: Array,
      onTaskClick: Function,
      relatives: Number,
      isEmpty: Number
    },

    mounted () {
      console.log(this.isEmpty)
    },
    methods: {
      taskClicked (task) {
        if (this.onTaskClick) this.onTaskClick(task)
      }
    }
  }
</script>



