<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <!-- 页头 -->
    <AnimateHeader
      bg-color="#4A90E2"
      menu-bg-color="#4586D2"
      color="#FFFFFF"
      title="办卡赚钱"
      sub-title="① 预留信息&nbsp; ② 申请办卡&nbsp; ③ 银行审批"
      :on-back-button-click="back"
      :expanded="true"
      :animate="false"
    >
    </AnimateHeader>

    <!--新进度提示小红点-->
    <div v-if="spot" class="spot"></div>

    <tab-layout :links="tabLinks" :bar-width="barWidth" bg-color="#FFF"
      :on-tab-click="onTabClick" use-content-slot="true" style="position: fixed; top: 100px; z-index: 10;"></tab-layout>

    <div class="page-content">
      <keep-alive>
        <list v-if="activeIndex == 0" :tasks="tasks" :relatives="relatives" :is-empty="empty"
        :on-task-click="onTaskClick"></list>
      </keep-alive>
      <keep-alive>
        <Progress v-if="activeIndex == 1" :progress-remind="progressRemind"
        :progress="progress"></Progress>
      </keep-alive>
    </div>
  </div>
</template>
<style lang="scss" scoped>
@import '../../../scss/mixins';

.page-content {
  background-color: #FFF;
  padding-top: 100px;
  padding-bottom: 20px;
}

.spot {
  position: fixed;
  display: inline-block;
  border-radius: 50%;
  top: 115px;
  right: 62px;
  width: 4px;
  height: 4px;
  background-color: #EA5A49;
  z-index: 11;
}

.credit-list-navbar {
  height: 100px;
  background: #4A90E2;

  .back-icon {
    display: inline-block;
    width: 60px;
    height: 100px;
    background: #4586D2;
    vertical-align: top;

    img {
      margin-top: 42px;
      width: 12px;
      height: 18px;
    }
  }

  .nav-information {
    display: inline-block;
    vertical-align: top;
    margin-top: 21px;
    margin-left: 15px;

    .title {
      @include font-bold();

      font-size: 20px;
      line-height: 20px;
      color: #FFFFFF;
      margin-bottom: 17px;
    }

    span {
      font-size: 13px;
      color: #FFFFFF;
    }
  }
}
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import TabLayout from '../TabLayout'
  import List from './List'
  import Progress from './Progress'
  import {CREDIT_TASK_PROGRESS} from 'constants'

  export default {
    components: {
      AnimateHeader,
      TabLayout,
      List,
      Progress
    },

    data () {
      return {
        tabLinks: [
          {
            to: { path: '/credit/list' },
            text: '任务列表'
          },
          {
            to: { path: '/credit/progress' },
            text: '进度查询'
          }
        ],
        barWidth: 375,
        activeIndex: 0,
        tasks: [],
        progress: [],
        relatives: 0,
        progressRemind: 0,
        empty: 0,
        spot: 0
      }
    },

    mounted () {
      this.activeIndex = _.indexOf(['list', 'progress'], this.$route.params.type) || 0
      this.refresh()
    },

    methods: {
      refresh () {
        $api.creditList()
        .then(res => res.data && res.data.payload)
        .then((payload) => {
          this.relatives = payload.relatives
          this.tasks = payload.tasks

          if (this.tasks.length === 0) {
            this.empty = 1
          }

          this.progress = payload.progress
          if (this.progress.length > 0) {
            this.progressRemind = 1
          }

          let progressId = $storage.get(CREDIT_TASK_PROGRESS) || []
          _.each(this.progress, (progress) => {
            let apiProgressId = progress.id + ':' + progress.mobile + ':' + progress.checking
            if (progressId.indexOf(apiProgressId) === -1) {
              this.spot = 1
            }
          })
        })
      },

      back () {
        let path = _.get(this.$route.query, 'backroute', '/dashboard')
        $router.back({ path })
      },

      onTabClick (index) {
        this.activeIndex = index
        if (index === 1 && this.spot === 1) {
          this.hideSpot()
        }
      },

      onTaskClick (task) {
        let route = {
          name: 'credit_detail',
          params: {
            id: task.id
          }
        }
        $router.forward(route)
      },

      hideSpot () {
        this.spot = 0
        let creditProgress = []
        _.each(this.progress, (progress) => {
          let apiProgressId = progress.id + ':' + progress.mobile + ':' + progress.checking
          creditProgress.push(apiProgressId)
        })
        $storage.set(CREDIT_TASK_PROGRESS, creditProgress)
      }
    }
}
</script>



